import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import "../styles/SideDrawer.scss";

class SideDrawer extends Component {
  state = {};
  render() {
    let drawerClasses = "side-drawer";
    if (this.props.show) {
      drawerClasses = "side-drawer open";
    }

    return (
      <nav className={drawerClasses} onClick={this.props.click}>
        <NavLink className="side-drawer_items" to="/">
          Home
        </NavLink>
        <NavLink className="side-drawer_items" to="/about">
          About
        </NavLink>
        <NavLink className="side-drawer_items" to="/service">
          Service
        </NavLink>
        <NavLink className="side-drawer_items" to="/contact">
          Contact
        </NavLink>
      </nav>
    );
  }
}

export default SideDrawer;
