import React, { Component } from "react";
import "../styles/contactall.scss";

class Contactall extends Component {
  state = {};
  render() {
    return (
      <div className="container-fluid contactall">
        <div className="contactall-p">
          <h1>
            <a
              target="_blanc"
              href="https://www.linkedin.com/company/byg-service"
            >
              <i className="faaa fa fa-linkedin" aria-hidden="true"></i>
            </a>
            &nbsp;
            <a target="_blanc" href="https://www.facebook.com/bygservices/">
              <i className="faaa fa fa-facebook" aria-hidden="true"></i>
            </a>
            &nbsp;
            <a target="_blanc" href="https://www.instagram.com/byg_services/">
              <i className="faaa fa fa-instagram" aria-hidden="true"></i>
            </a>
            &nbsp;
            <a target="_blanc" href="https://twitter.com/BygServices">
              <i className="faaa fa fa-twitter" aria-hidden="true"></i>
            </a>
          </h1>
        </div>
        <p className="contactall-paragraph">
          BYG Services, a trusted sourcing agency that bridges the communication
          , helps avoid misalignment, and facilitates good business between
          buyers and suppliers. Want to work with us and grow your business?
        </p>
        <span className="getstarted-div grow">
          <a href="https://byg-services.com/#/contact">
            <span className="getstarted">Let's get started now</span>
          </a>
        </span>
      </div>
    );
  }
}

export default Contactall;
