import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import DrawerToggleButton from "../Drawer/DrawerToggleButton";
import logo from "../images/logo1.jpg";
import "../styles/navigation.scss";
class Navigation extends Component {
  state = {};

  render() {
    return (
      <header className="container-fluid toolbar">
        <nav className="toolbar__navigation">
          <div className="toolbar__logo">
            <a href="https://byg-services.com/#/">
              <img src={logo} alt="" width="50px" height="50px" />
            </a>
          </div>
          <div className="spacer" />
          <div>
            <NavLink className="toolbar_navigation-items" to="/">
              Home
            </NavLink>
            <NavLink className="toolbar_navigation-items" to="/about">
              About
            </NavLink>
            <NavLink className="toolbar_navigation-items" to="/service">
              Services
            </NavLink>
            <NavLink className="toolbar_navigation-items" to="/contact">
              Contact
            </NavLink>
          </div>
          <div className="toolbar__toggle-button">
            <DrawerToggleButton click={this.props.drawerClickHandler} />
          </div>
        </nav>
      </header>
    );
  }
}

export default Navigation;
