import React, { Component } from "react";
import "../styles/contact.scss";
import ourlogo from "../images/logo.jpg";
import Contactall from "./contactall";
class Contact extends Component {
  state = {};
  render() {
    return (
      <div>
        <div className="container-fluid contact tc">
          <h1 className="contact-h1">CONTACT US </h1>

          <p className="contact-p">
            For more information about us and our services, get in touch with
            us. We will get back to you as soon as possible.
          </p>

          <br />
          <div className="row card-row">
            <div className="col-md-4 tachyon">
              <div className="pt4 br4 ba dark-gray b--black-10 shadow-2 center">
                <div className="pa2 ph3-ns pb3-ns">
                  <div className="tc">
                    <i className="faaa fa fa-phone "></i>
                  </div>
                  <div className="tc b pa3 pt4">
                    <p>PHONE / WHATSAPP</p>
                  </div>
                  <div className="tc">
                    <p>+90 552 558 8523</p>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-4 tachyon">
              <div className="pt4 br4 ba dark-gray b--black-10 shadow-2 center">
                <div className="pa2 ph3-ns pb3-ns">
                  <div className="tc">
                    <i className="faaa fa fa-envelope"></i>
                  </div>
                  <div className="tc b pa3 pt4">
                    <p>EMAIL</p>
                  </div>
                  <div className="tc">
                    <p>contact@byg-services.com</p>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-4 tachyon ">
              <div className="pt4 br4 ba dark-gray b--black-10 shadow-2 center">
                <div className="pa2 ph3-ns pb3-ns ">
                  <div className="tc">
                    <i className="faaa fa fa-globe"></i>
                  </div>
                  <div className="tc b pa3 pt4">
                    <p>WEBSITE</p>
                  </div>
                  <div className="tc">
                    <p>www.byg-services.com</p>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="mylogo">
            <img className="mylogo-img" src={ourlogo} alt="" />
          </div>
        </div>
        <Contactall />
      </div>
    );
  }
}

export default Contact;
