import React, { Component } from "react";
import mts from "../images/MTS.jpg";
import eraslan from "../images/eras1.jpg";
import "../styles/partners.scss";
class Partners extends Component {
  state = {};
  render() {
    return (
      <div className="container-fluid partnerall">
        <div>
          <div className="partner">
            <h1 className="partner-h1">OUR REFERENCES</h1>
            <p className="partner-p">
              Some companies we work with. They are satisfied with our services
              which makes us win their trust
            </p>
            <div className="partner-img">
              <img className="ers-img" src={eraslan} alt="" />
              <img className="mts-img" src={mts} alt="" />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Partners;
