import React, { Component } from "react";
import "../styles/about.scss";
import bag from "../images/baguiri.jpg";
import Contactall from "./contactall";

class About extends Component {
  state = {};
  render() {
    return (
      <div>
        <div className="container-fluid about">
          <div className="about-container">
            <div className="about-section">
              <h2 className="about-head">ABOUT US</h2>
              <div className="about-p">
                <p className="p-element">
                  We work as sourcing agents and assist our customers import
                  products from Turkey. Turkey is well-known with its large
                  trading industry, best and high quality products. However, we
                  understand that it is very hard for small business importers
                  to find reliable suppliers, good prices and quality.
                  Therefore, we help our customers find right suppliers, best
                  products and competitive prices. We also help Turkish
                  manufacturers find clients and sell their products in the
                  African market. So, we act as bridge or intermediary between
                  manufacturers and importers.
                </p>
              </div>

              <div className="misvision">
                <span className="misvision-span"> Our Vision</span>
                <p className="misvision-p">
                  To offer our services to each and all.
                </p>
                <br />
                <span className="misvision-span">Our Mission</span>
                <p className="misvision-p">
                  To provide most appropriate and satisfying services that
                  contribute to business growth.
                </p>
              </div>
            </div>

            <div className="founder-section">
              <h2 className="about-head">ABOUT FOUNDER</h2>
              <div className="about-img-section">
                <img
                  className="about-img"
                  src={bag}
                  alt=""
                  width="250px"
                  height="100%"
                />
              </div>
              <div className="about-p">
                <p>
                  I am Baguiri YAROU GOUDOU. I am an Electrical and Electronics
                  Engineer, a marketer and a young entrepreneur. I decided to
                  create this one-stop to help small businesses grow by
                  providing comprehensive sourcing services.
                  <br /> I hold a BSc degree in Electrical and Electronics
                  Engineering from the University of Turkish Aeronautical
                  Association in Ankara/Turkey and am currently doing a Master's
                  degree in Energy Economics and Management at Social Sciences
                  University of Ankara. I'm versatile and highly flexible with a
                  solid background in energy sector, in marketing strategies, as
                  well as website development. I have excellent communication
                  skills and am fluent in multiple languages, including French,
                  English and Turkish.
                </p>
              </div>
            </div>
          </div>
        </div>
        <Contactall />
      </div>
    );
  }
}

export default About;
