import React, { Component } from "react";
import "../styles/service.scss";
import Contactall from "./contactall";

class Service extends Component {
  state = {};
  render() {
    const check = <i className="fa fa-check-circle circle1"></i>;
    const check1 = <i className="fa fa-check-circle circle2"></i>;
    const check2 = <i className="fa fa-check-circle circle3"></i>;
    return (
      <div>
        <div className="container-fluid service tc">
          <h1 className="service-header">OUR SERVICES</h1>

          <div className="row">
            <div className="col-md-4 tachyon-service  ">
              <div className="pt4 br4 ba dark-gray b--black-10  center">
                <div className="pa2 ph3-ns pb3-ns">
                  <div className="tc">
                    <h3 className="headind-h3 b head1">SOURCING PROCESS</h3>
                    <p className="tl">
                      The steps of our sourcing services are as follows:
                    </p>
                  </div>
                  <div className="tachyon-check tl ">
                    <p>
                      {check} Discuss with the client for products
                      specifications
                    </p>
                    <p>{check} Look for a suitable supplier</p>
                    <p> {check} Get quote from the supplier</p>
                    <p>
                      {check} Arrange production after confirmation from the
                      client
                    </p>
                    <p> {check} Oversee the production process</p>
                    <p> {check} Arrange factory visit for inspection</p>
                    <p> {check} Arrange shipment</p>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-4 tachyon-service ">
              <div className="pt4 br4 ba dark-gray b--black-10  center">
                <div className="pa2 ph3-ns pb3-ns">
                  <div className="tc">
                    <h3 className="headind-h3 b head2">AREAS OF EXPERTISE</h3>
                    <p className="tl">
                      Our areas of expertise include, but are not limited to:
                    </p>
                  </div>
                  <div className="tachyon-check tl ">
                    <p>{check1} Solar water heating systems</p>
                    <p>{check1} Solar PV systems</p>
                    <p> {check1} Cables</p>
                    <p> {check1} Iron &amp; Steel</p>
                    <p> {check1} Spare parts</p>
                    <p> {check1} Textile</p>
                    <p> {check1} Construction materials</p>
                    <p> {check1} Medical equipments</p>
                    <p> {check1} Agriculture Equipments</p>
                    <p> {check1} Flour</p>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-4 tachyon-service ">
              <div className="pt4 br4 ba dark-gray b--black-10 center">
                <div className="pa2 ph3-ns pb3-ns">
                  <div className="tc">
                    <h3 className="headind-h3 b head3">DIGITAL MARKETING</h3>
                    <p className="tl">
                      We are also digital marketers, helping small businesses
                      promote their products and services using numerous digital
                      tactics and online based digital technologies to connect
                      with customers.
                    </p>
                  </div>
                  <div className="tachyon-check tl ">
                    <p>{check2} Social Media Marketing</p>
                    <p> {check2} Affiliate marketing</p>
                    <p> {check2} Search Engine Optimization (SEO)</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <Contactall />
      </div>
    );
  }
}

export default Service;
