import React, { Component } from "react";

import "../styles/home.scss";

import Contactall from "./contactall";
import Partners from "./partners";

import Servicehome from "./servicehome";
import Servicehomemobile from "./servicehomemobile";

class Home extends Component {
  state = {};
  render() {
    return (
      <div>
        <div className="container-fluid home">
          <h2 className="home-content">
            Is it difficult for you to find reliable suppliers, competitive
            prices or to import products from Turkey?
          </h2>
          <p className="home-content-p">
            BYG-SERVICES is opened to help you find the right manufacturers and
            right products at the right time.
          </p>
        </div>
        <div className="home-body">
          <div className="home-body-content">
            <h1 className="home-body-h1">WHY YOU SHOULD CHOOSE US</h1>
            <p className="home-body-p">
              The purpose of our services is to look after our customers'
              interests. Therefore, We act as their representative and make them
              feel as they are in Turkey. No matter the quantity of their
              orders, we take them seriouly, make sure that they get very
              competitive prices, and review quality control because we are
              looking for a long term business partnership.
            </p>
          </div>
        </div>
        <Servicehome />
        <Servicehomemobile />

        <Partners />
        <Contactall />
      </div>
    );
  }
}

export default Home;
